import { useXField } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { StyledCheckbox } from '../../atoms/StyledCheckbox'

interface IProps extends LegacyOmit<React.InputHTMLAttributes<never>, 'name'> {
  fieldName: FieldName
  label?: string
  children?: never
}

export type FormInputCheckboxProps = IProps

export const FormCheckbox: React.FC<IProps> = ({
  fieldName,
  label,
  children,
  ...inputProps
}) => {
  const field = useXField<any>(fieldName)
  return useObserver(() => (
    <StyledCheckbox>
      <input
        type="checkbox"
        id={field.name}
        name={field.name}
        checked={field.value === true}
        onChange={() => field.setValue(!field.value)}
        {...inputProps}
      />
      {label && <label htmlFor={field.name}>{label}</label>}
    </StyledCheckbox>
  ))
}
